lor: $font-color-primary;\r\n}\r\n\r\n//Utilities\r\n\r\n.flex-it {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    text-align: center;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n\r\n//For animated background\r\n@keyframes scrolling {\r\n\r\n    0% {\r\n        transform: translateX(0px);\r\n    }\r\n\r\n    50% {\r\n        transform: translateX(-300px);\r\n    }\r\n\r\n    100% {\r\n        transform: translateX(0px);\r\n    }\r\n}\r\n\r\n.background {\r\n    z-index: -1000;\r\n    position: fixed;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n}\r\n\r\n.background::before {\r\n    content: '';\r\n    position: absolute;\r\n    background-position-y: center;\r\n    box-sizing: content-box;\r\n    background-size: cover;\r\n    width: 200%;\r\n    height: 100%;\r\n    background-image: url('.././img/normal.jpg');\r\n    animation-name: scrolling;\r\n    animation-duration: 60s;\r\n    animation-iteration-count: infinite;\r\n    animation-timing-function: ease-in-out;\r\n}\r\n\r\n//For the animation of the weather icons in the cards\r\n.weather-icon {\r\n    animation-name: weather-icon;\r\n    animation-duration: 5s;\r\n    animation-iteration-count: infinite;\r\n    animation-timing-function: ease-in-out;\r\n}\r\n\r\n","@use \"main\";\r\n\r\n//Sidebar C-F Button\r\nnav.navbar section .menu li {\r\n  #C-F-btn {\r\n    height: 5vh;\r\n    width: 3rem;\r\n    border: none;\r\n    color: main.$font-color-primary;\r\n    background: main.$secondary-color;\r\n    border-radius: 30%;\r\n    margin: auto 5%;\r\n  }\r\n  #C-F-btn:hover {\r\n    opacity: 0.7;\r\n  }\r\n  //Toggles display for cel and far button to control imperial vs metric measurement\r\n  //Upon click the javascript with change the class of #C-F-btn to C or F then the CSS takes effect\r\n  .C .c-unit,\r\n  .F .f-unit {\r\n    border: 2px solid white;\r\n    display: none;\r\n    height: inherit;\r\n    width: inherit;\r\n    border-radius: inhe