ine\r\n        li.temp {\r\n          text-align: center;\r\n          align-items: center;\r\n          font-size: main.$font-size-mid;\r\n        }\r\n\r\n        //Precipitation line\r\n        li.precipitation {\r\n          text-align: center;\r\n\r\n          figure {\r\n            display: flex;\r\n            flex-direction: row;\r\n            justify-content: center;\r\n            align-items: center;\r\n\r\n            progress {\r\n              width: 70%;\r\n              margin: 1vh 5px;\r\n              height: 2vh;\r\n            }\r\n          }\r\n\r\n        }\r\n\r\n        //The description of the forcast\r\n        li.desc {\r\n          text-align: center;\r\n          font-size: main.$font-size-mid;\r\n          border-bottom: 1px main.$secondary-color solid;\r\n          margin: 0.5vh 0;\r\n          line-height: 2rem;\r\n        }\r\n      }\r\n\r\n      //The Details for the Weather Cards\r\n      .misc {\r\n        li {\r\n          width: 14vw;\r\n          text-align: justify;\r\n          padding: 1vh 1vw;\r\n          font-size: main.$font-size-mid;\r\n        }\r\n      }\r\n    }\r\n\r\n    .weather-card.visable {\r\n      display: flex;\r\n      opacity: 1;\r\n      transition: all 0.4s ease;\r\n    }\r\n  }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  body {\r\n    overflow-y: scroll;\r\n  }\r\n\r\n  main {\r\n    justify-content: start;\r\n\r\n    #container {\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n      justify-content: start;\r\n      padding: 0;\r\n\r\n      .weather-card {\r\n        margin: 11vh 0;\r\n        height: 70vh;\r\n        width: 80vw;\r\n        min-height: 575px;\r\n        flex-direction: column;\r\n\r\n        .misc {\r\n          display: flex;\r\n          flex-direction: row;\r\n          flex-wrap: wrap;\r\n          align-items: center;\r\n          justify-content: center;\r\n\r\n          li {\r\n            height: 5vh;\r\n            width: 15vw;\r